ul,
ol {
  margin: 0 0 2em 0.4em;
  padding-left: 2em;
  line-height: 1.62;
}
ul {
  list-style-type: square;
}
ol {
  list-style-type: decimal;
}
li {
  margin-bottom: 0.2em;
}
li ul,
li ol {
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 14px;
}
ul li ul li {
  list-style-type: disc;
}
ul li ul li ul li {
  list-style-type: circle;
}
ol li ol li {
  list-style-type: upper-roman;
}
ol li ol li ol li {
  list-style-type: lower-roman;
}
ol li p {
  margin: 0.4em 0 0.6em;
}
ol li p::before {
  content: none;
}
