figure {
  text-align: center;
}
figure.left,
figure.right {
  width: 50%;
}
figcaption {
  font-size: 80%;
  margin-top: 0.5em;
}
figcaption::after {
  content: "";
  width: 15%;
  display: block;
  margin: 1em auto 0.2em;
  color: #888;
  border-bottom: 1px solid #ddd;
}
@media screen and (max-width: 768px) {
  figure.left,
  figure.right {
    width: 100%;
    margin: 0;
    padding: 1em 40px;
    float: none !important;
  }
}
