.card {
  padding: 0;
  background: #fff;
  margin-bottom: 1em;
  border-radius: 3px;
  position: relative;
  padding-bottom: 0.8em;
  -webkit-box-shadow: 0 1px 2px 1px rgba(0,0,0,0.2);
          box-shadow: 0 1px 2px 1px rgba(0,0,0,0.2);
}
.card.card-rich .card-head {
  padding: 0;
  margin: 0 -1px;
  min-height: 50px;
  position: relative;
  margin-bottom: 1.8em;
}
.card.card-rich .card-head img,
.card.card-rich .card-head .card-rich-img img,
.card.card-rich .card-head img.card-rich-img {
  margin: 0;
  border-top-radius: 3px;
}
.card.card-rich .card-head .card-title,
.card.card-rich .card-head .card-subtitle {
  left: 0;
  bottom: 0;
  color: #fff;
  width: 100%;
  padding: 0 0.8em;
  font-size: 1.8em;
  position: absolute;
  margin-bottom: 0.6em;
  font-weight: normal;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.3);
}
.card.card-rich .card-head .card-subtitle {
  left: auto;
  right: 0;
  font-size: 1em;
  text-align: right;
  margin-bottom: 1.4em;
}
.card.z-radius.z-rich .card-head img,
.card.z-radius.z-rich .card-head .card-rich-img img,
.card.z-radius.z-rich .card-head img.card-rich-img {
  border-top-radius: 5px;
}
.card * + .card-rich-img {
  border-top-radius: 0;
}
.card .card-head {
  padding: 0.8em 1.5em 1em;
}
.card .card-head .card-title {
  font-size: 1.8em;
  margin-bottom: 0;
}
.card .card-head .card-subtitle {
  margin-top: -0.8em;
  margin-bottom: 0;
}
.card .card-head .card-rich-img {
  margin: 0.8em -1.5em 0.8em;
  text-align: center;
}
.card .card-head .card-rich-img img {
  margin: 0;
}
.card .card-head img.card-rich-img {
  margin: 0.8em 0;
}
.card .card-rich-img + .card-head {
  padding-top: 0.4em;
}
.card .card-content {
  padding: 0.8em 1.5em;
  color: #333;
  line-height: 1.825;
}
.card img + .card-header {
  margin-top: 0;
}
.card * + .card-content {
  padding-top: 0;
}
.card .card-head + .card-content {
  margin-top: -0.8em;
}
.card .card-bar {
  width: 100%;
  padding: 0 1.5em;
  line-height: 50px;
  margin-bottom: -0.8em;
  border-top: 1px solid #ddd;
  background: #f7f7f7;
}
.card .card-bar .btn {
  margin: 1em 0.5em 1em 0;
}
