.acommets {
  position: relative;
}
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .acommets {
    width: 80%;
  }
}
.commet {
  right: -15px;
  width: 120px;
  font-size: 0.9em;
  padding-top: 0.4em;
  position: absolute;
  font-style: normal;
  font-weight: normal;
  margin-right: -120px;
}
.commet:before {
  top: 0;
  left: 0;
  width: 40%;
  content: '';
  position: absolute;
  border-top: 1px solid #888;
}
.commet.commet-left {
  left: -15px;
  right: auto;
  text-align: right;
  margin-right: 0;
  margin-left: -120px;
}
.commet.commet-left:before {
  right: 0;
  left: auto;
}
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .commet {
    width: 20%;
    margin-right: -20%;
  }
  .commet.commet-left {
    left: auto;
    right: -15px;
    margin-left: 0;
    text-align: left;
    margin-right: -20%;
    margin-top: 10px;
  }
  .commet.commet-left:before {
    left: 0;
    right: auto;
  }
}
@media screen and (max-width: 768px) {
  .commet {
    position: static;
    margin-right: 0;
  }
  .commet.commet-left {
    margin: 0;
  }
  .commet:before {
    border: 0;
    content: '( ';
    position: static;
    padding-left: 5px;
  }
  .commet:after {
    content: ' )';
  }
}
