tt,
code,
pre {
  font-family: 'Source Code Pro', Monaco, Menlo, Consolas, "Courier New", monospace;
}
tt,
code {
  font-size: 0.75em;
  margin: 0 0.3em;
  padding: 0.1em 0.5em;
  border-radius: 3px;
  word-break: break-all;
  background: #f7f7f7;
  border: 1px solid #ddd;
}
pre {
  display: block;
  overflow: auto;
  padding: 0.8em 1.3em;
  margin: 1.6em -1.3em;
  font-size: 0.85em;
  line-height: 1.5;
  color: #888;
  background: #f7f7f7;
  border-left: 2px solid #ddd;
  -webkit-transition: 0.3s ease border;
  transition: 0.3s ease border;
}
pre:hover {
  border-left-color: #888;
}
pre code,
pre tt {
  margin: 0;
  padding: 0;
  border: none;
  font-size: 1rem;
  background: none;
}
blockquote {
  padding: 0 1.3em;
  margin: 1em -1.3em 0;
  line-height: 1.62;
  color: #888;
  border-left: 2px solid #ddd;
}
blockquote a {
  color: #bbb;
}
blockquote p {
  margin-bottom: 0.5em;
}
blockquote p:last-child {
  margin-bottom: 0;
}
blockquote b,
blockquote em,
blockquote cite,
blockquote strong {
  color: #777;
}
blockquote + p {
  margin-top: 1em;
}
