.nav {
  width: 100%;
  z-index: 1000;
  background-color: rgba(0,0,0,0.4);
}
.nav.fixed {
  position: fixed;
  top: 0;
  left: 0;
}
.nav a {
  color: #fff;
  line-height: 3rem;
  text-decoration: none;
}
.nav a.nav-band {
  font-weight: bold;
}
.nav .container {
  min-height: 3rem;
}
.nav ul.nav-group {
  margin: 0 !important;
}
.nav ul.nav-group li {
  float: left;
  margin: 0;
  list-style: none;
  -webkit-transition: 0.3s ease;
  transition: 0.3s ease;
}
.nav ul.nav-group li a {
  display: block;
  padding: 0 0.85em;
}
.nav ul.nav-group li::hover {
  background-color: rgba(0,0,0,0.2);
}
.nav ul.nav-group li.active,
.nav ul.nav-group li::active {
  background-color: rgba(0,0,0,0.6);
}
