h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  color: #333;
  font-family: "Segoe UI", "Lucida Grande", Helvetica, Arial, "Microsoft YaHei", FreeSans, Arimo, "Droid Sans", "wenquanyi micro hei", "Hiragino Sans GB", "Hiragino Sans GB W3", sans-serif;
}
h1 + hr,
h2 + hr,
h3 + hr,
h4 + hr,
h5 + hr,
h6 + hr {
  width: 100%;
  border-top-width: 1px;
  margin-top: 0;
}
h1 {
  font-size: 2.5em;
  margin: 0.6em 0 0.4em;
}
h2 {
  font-size: 1.8em;
  margin: 0.8em 0 0.5em;
}
h3 {
  font-size: 1.5em;
  margin: 0.7em 0;
}
h4 {
  font-weight: bold;
  font-size: 1.2em;
  line-height: 1.5;
  margin: 1em 0;
}
h5,
h6 {
  font-size: 1em;
  font-weight: bold;
  line-height: 1.86;
  margin: 1em 0;
}
