table {
  margin-bottom: 1.5em;
  border-spacing: 0;
  border-collapse: collapse;
}
table th,
table td {
  padding: 0.7rem;
  line-height: 1.6;
  vertical-align: top;
  border-top: 1px solid #dbdbdb;
}
table th[scope="row"],
table td[scope="row"] {
  color: color-gray;
}
table thead th,
table thead td {
  border-top: 0;
  vertical-align: bottom;
  border-bottom: 1px solid #dbdbdb;
}
table tbody + tbody tr:first-child td {
  border-top: 2px solid #ddd;
}
tr.active th,
tr.active td {
  border-left-color: #eaea90 !important;
  background-color: #ffff85 !important;
}
.table {
  width: 100%;
}
.table-bordered {
  border: 1px solid #dbdbdb;
}
.table-bordered th,
.table-bordered td {
  border-left: 1px solid #dbdbdb;
}
.table-bordered th:first-child,
.table-bordered td:first-child {
  border-left: 0;
}
.table-striped tbody tr:nth-child(odd) th,
.table-striped tbody tr:nth-child(odd) td {
  background-color: #f7f7f7;
}
.table-hover tbody tr:hover th,
.table-hover tbody tr:hover td {
  background-color: #e8e8e8;
}
.table-center th,
.table-center td {
  text-align: center;
}
.table-responsive {
  min-hieght: 0.01%;
  overflow-x: auto;
}
@media screen and (max-width: 768px) {
  .table-responsive {
    width: 100%;
    overflow-y: hidden;
  }
}
