body {
  font-weight: 400;
  color: #333;
  font-family: "Segoe UI", "Lucida Grande", Helvetica, Arial, "Microsoft YaHei", FreeSans, Arimo, "Droid Sans", "wenquanyi micro hei", "Hiragino Sans GB", "Hiragino Sans GB W3", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.text-indent p::before {
  content: "\3000\3000";
  display: inline;
}
p {
  margin-top: 0;
  margin-bottom: 1em;
  line-height: 1.86;
  word-break: break-all;
}
p.text-author {
  display: block;
  text-align: right;
  font-weight: 800;
}
p.text-author::before {
  content: '——' !important;
}
a {
  color: #111;
}
a img {
  border: 0;
}
img {
  max-width: 100%;
}
b,
em,
cite,
strong {
  color: #333;
  font-weight: 700;
}
b a,
em a,
cite a,
strong a {
  color: inherit;
}
em,
cite {
  font-style: normal;
}
em a,
cite a {
  font-style: normal;
}
em {
  font-style: inherit;
  font-weight: inherit;
  text-decoration: inherit;
  -webkit-text-emphasis: filled circle;
          text-emphasis: filled circle;
  -webkit-text-emphasis-position: under;
          text-emphasis-position: under left;
}
i {
  font-style: italic;
}
img {
  margin: 0.2em 0;
}
sub a,
sup a {
  text-decoration: none;
}
hr {
  width: 20%;
  display: block;
  border: 0 none;
  margin: 2em auto;
  border-top: 2px solid #ddd;
}
abbr[title],
dfn[title] {
  cursor: help;
  border-bottom: 1px dotted;
}
dl {
  margin-bottom: 0.2em;
}
dt {
  padding: 0;
  font-weight: 800;
}
dd {
  margin-left: 0;
}
dt + dd {
  margin-top: 0.2em;
}
dd + dt {
  margin-top: 0.6em;
}
[draggable] {
  cursor: move;
}
mark {
  color: #333;
  background: #fffcc4;
  border-bottom: 1px solid #ffedce;
}
kbd {
  color: #fff;
  font-size: 90%;
  padding: 2px 4px;
  border-radius: 3px;
  background-color: #333;
  font-family: 'Source Code Pro', Monaco, Menlo, Consolas, "Courier New", monospace;
  -webkit-box-shadow: inset 0 -1px 0 rgba(0,0,0,0.25);
          box-shadow: inset 0 -1px 0 rgba(0,0,0,0.25);
}
kbd kbd {
  padding: 0;
}
