.poem-horizontal {
  text-align: center;
  margin: 2em 0;
}
.poem-horizontal p {
  margin-bottom: 0.2em;
}
.poem-horizontal p:before {
  content: none;
}
.poem-horizontal .poem-author {
  display: block;
  text-align: right;
  font-weight: 800;
}
.poem-horizontal .poem-author:before {
  content: '——';
}
.poem-vertical-left,
.poem-vertical-right {
  display: block;
  white-space: pre-line;
  margin: 2em auto;
  line-height: 1.4em;
  -webkit-writing-mode: vertical-lr;
  -ms-writing-mode: tb-lr;
      writing-mode: vertical-lr;
}
.poem-vertical-left h1,
.poem-vertical-right h1,
.poem-vertical-left h2,
.poem-vertical-right h2,
.poem-vertical-left h3,
.poem-vertical-right h3,
.poem-vertical-left h4,
.poem-vertical-right h4,
.poem-vertical-left h5,
.poem-vertical-right h5,
.poem-vertical-left h6,
.poem-vertical-right h6,
.poem-vertical-left .poem-title,
.poem-vertical-right .poem-title {
  margin: 0;
  margin-left: -0.5em;
  font-weight: bold;
  font-family: Georgia, "Times New Roman", Times, SimSun, serif;
}
.poem-vertical-left h1:before,
.poem-vertical-right h1:before,
.poem-vertical-left h2:before,
.poem-vertical-right h2:before,
.poem-vertical-left h3:before,
.poem-vertical-right h3:before,
.poem-vertical-left h4:before,
.poem-vertical-right h4:before,
.poem-vertical-left h5:before,
.poem-vertical-right h5:before,
.poem-vertical-left h6:before,
.poem-vertical-right h6:before,
.poem-vertical-left .poem-title:before,
.poem-vertical-right .poem-title:before {
  content: none;
}
.poem-vertical-left h1 .poem-author,
.poem-vertical-right h1 .poem-author,
.poem-vertical-left h2 .poem-author,
.poem-vertical-right h2 .poem-author,
.poem-vertical-left h3 .poem-author,
.poem-vertical-right h3 .poem-author,
.poem-vertical-left h4 .poem-author,
.poem-vertical-right h4 .poem-author,
.poem-vertical-left h5 .poem-author,
.poem-vertical-right h5 .poem-author,
.poem-vertical-left h6 .poem-author,
.poem-vertical-right h6 .poem-author,
.poem-vertical-left .poem-title .poem-author,
.poem-vertical-right .poem-title .poem-author {
  font-size: 60%;
}
.poem-vertical-left p,
.poem-vertical-right p {
  margin: 0 -0.3em;
  text-align: left;
  max-height: 600px;
  letter-spacing: inherit;
  word-wrap: break-word;
  white-space: pre-line;
  line-height: 1em;
}
.poem-vertical-left p:before,
.poem-vertical-right p:before {
  content: none;
}
.poem-vertical-left {
  -webkit-writing-mode: vertical-lr;
  -ms-writing-mode: tb-lr;
      writing-mode: vertical-lr;
}
