.absolute {
  position: absolute;
}
.relative {
  position: relative;
}
.fixed {
  position: fixed;
}
.left {
  float: left !important;
}
.right {
  float: right !important;
}
.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.block {
  display: block !important;
}
.inline {
  display: inline !important;
}
.inline-block {
  display: inline-block !important;
}
.hide {
  display: none !important;
  visibility: hidden !important;
}
.serif {
  font-family: Georgia, "Times New Roman", Times, SimSun, serif;
}
.kai {
  font-family: Georgia, "Times New Roman", Times, Kai, "Kaiti SC", KaiTi, BiauKai, serif;
}
.monospace {
  font-family: 'Source Code Pro', Monaco, Menlo, Consolas, "Courier New", monospace;
}
.container {
  max-width: 80%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.container:before,
.container:after {
  content: " ";
  display: table;
}
.container:after {
  clear: both;
}
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .container {
    max-width: 92%;
  }
}
@media screen and (max-width: 768px) {
  .container {
    max-width: 100%;
    padding-left: 1em;
    padding-right: 1em;
  }
}
.radius {
  border-radius: 5px;
}
.circle {
  border-radius: 99999999px;
}
